<template>
  <div class="workContents" :style="{height:height + 'px'}">
    <transition name="textSlide">
      <div class="workTitleStyle" v-show="showData['showAboutTitle']">
        About
      </div>
    </transition>
    <transition name="slide">
      <img v-show="showData['showArrow']" class="setArrowPos" :src="imageData['arrowImage']">
    </transition>
    <v-row class="mainBody" align="center" no-gutters>
      <v-col
        cols="12"
        sm="12"
        md="5"
        lg="5"
        xl="5"
        class="ml-3 mr-3"
      >
        <v-img
          :src="imageData['profileImage']"
          width="95%"
          height="95%"
        ></v-img>
      </v-col>
      <v-col cols="1"></v-col>
      <v-col
        cols="12"
        sm="12"
        md="5"
        lg="5"
        xl="5"
        style="font-size:2.0vw; color:gray;"
      >
        <span>
          岡田　尋嗣<br><br>
          1997年5月6日生まれ、仙台高等専門学校名取キャンパス機械
          システム工学科卒。株式会社オーパス所属。
          航空宇宙業界から転身し、2019年から一からフロントエンジニア
          としてキャリアスタート。現在は、中規模Webアプリケーション
          開発、UIデザイン及びコンポーネント制作を手掛ける。
          20代前半という研鑽の時期を日々精進中。
        </span>
      </v-col>
    </v-row>
  </div>
</template>

<script>

export default {
  name: 'aboutContents',
  props: {
    height: {
      type: Number,
    }
  },
  data () {
    return {
      imageData: {
        arrowImage:require("@/assets/fullWidthArrow.svg"),
        profileImage: require("@/assets/myImage.svg"),
      },
      showData: {
        showArrow: false,
        showAboutTitle: false
      }
    }
  },
  mounted() {
    setTimeout(this.slideArrow, 200);
    setTimeout(this.slideName, 1000);
  },
  methods: {
    slideArrow() {
      this.showData['showArrow'] = true;
    },
    slideName() {
      this.showData['showAboutTitle'] = true;
    }
  }
}
</script>

<style scoped>
.workContents {
  position: relative;
  height: 85px;
}

.workTitleStyle {
  float: right;
  font-size: 3.2vw;
  font-style: oblique;
  font-weight: 1000;
  color: #42AEEF;
  margin-right: 7%;
  letter-spacing: 5px;
}

.setArrowPos {
  position: absolute;
  z-index: 5;
  left:0;
  width:100%;
  margin-top: 2.5%;
}

.mainBody {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 5;
  margin-top: 6%;
}

.slide-enter-active, .slide-leave-active {
  transition: transform .6s
}
.slide-enter {
  transform: translateX(-1000px)
}

.textSlide-enter-active, .textSlide-leave-active {
  transition: transform .28s
}
.textSlide-enter {
  opacity: 0;
  transform: translateX(-150px)
}

</style>



